<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process109"></a>

<p><br></p>
<p><span class="rvts17">大逃亡（</span><span class="rvts50">The Escape</span><span class="rvts17">）</span><span class="rvts50">.</span><span class="rvts17">上篇</span><span class="rvts50">[</span><span class="rvts17">主线</span><span class="rvts50">]</span></p>
<p class="rvps2"><span class="rvts41">起始坐标：</span>
<br><span class="rvts21">地点：欢乐堡任何位置</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">逃离欢乐堡</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1009.jpg" display="block" background-size="contain|cover"><span class="rvts39"></span>
<br><span class="rvts77">来到欢乐堡后，需要尽可能多的打听当地居民对于</span><span class="rvts20"></span><span class="rvts21">逃离欢乐堡</span><span class="rvts20"></span><span class="rvts21">的看法和一些见解，这样可以更新你的游戏日志，并寻找到更多的逃脱方法。</span>
<br><span class="rvts21">需要注意的是，一旦逃离了欢乐堡，再次返回这里后，所有的守卫都将变得敌对并攻击你。当然，想要逃脱欢乐堡，并不是一件非常难的事，可以用很多种办法进行逃离。</span>
<br><span class="rvts21">笔者就介绍最常用的几种方法：</span>
<br><span class="rvts21">方法一：</span>
<br><span class="rvts21">直接破门而入。</span>
<br><span class="rvts21">欢乐堡要塞大门由</span><span class="rvts20">4</span><span class="rvts21">个守卫和</span><span class="rvts20">1</span><span class="rvts21">只狗把守，另外，在欢乐堡城中，还有一个遛狗的守卫。因此，如果选择这种办法，需要较好的装备支持和较高的等级支持。</span>
<br><span class="rvts21">如果是游戏前期，并不建议使用这样的方法。这</span><span class="rvts20">4</span><span class="rvts21">个守卫中，有进入欢乐堡要塞的各种钥匙。只要拿着钥匙，就可以进入要塞里面进行探索。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法二：</span>
<br><span class="rvts21">这种办法比较容易，但是也是较为风险。可以在城中偷东西（需要偷盗技能），并且需要被抓住后送进监狱，之后从监狱部分开始探索并逃离。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法三：</span>
<br><span class="rvts21">需要完成支线任务</span><span class="rvts20"></span><span class="rvts21">传送装置（</span><span class="rvts20">The Teleporter</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，在获得传送手套后，把一名队友传送到城堡要塞大门旁边的一处坍塌的楼层的第二层。</span>
<br><span class="rvts21">之后让队友踢下二层的活梯子，使所有人都爬上要塞，进入卢克斯的休息室，然后进行逃脱。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法四：</span>
<br><span class="rvts21">依然需要先完成支线任务</span><span class="rvts20"></span><span class="rvts21">传送装置（</span><span class="rvts20">The Teleporter</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，之后赶往初始遇到同伴费恩的地点，使用那里的地形，传送到下层海岸边，并进入监狱的犯人折磨区进行逃脱。</span>
<br><span class="rvts21">不过可能会面对这里的小</span><span class="rvts20">BOSS</span><span class="rvts21">奈尔思（</span><span class="rvts20">Kniles</span><span class="rvts21">），打败他，或交给他在刚开始来到欢乐堡大门处遇到的那个割舌头后又被爆尸的尸骸后，便可以通过下水道的秘门。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法五：</span>
<br><span class="rvts21">在犯人折磨区，可以找到有两个护法者正在恐吓一个小孩子。通过劝阻或拯救后，孩子会让你使用船逃离欢乐堡。详见本章节支线</span><span class="rvts20"></span><span class="rvts21">只是孩子们的小把戏</span><span class="rvts20"></span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法六：</span>
<br><span class="rvts21">通过欢乐要塞二层，会遇到一个骑士正在和里面的护法者干架，帮助骑士，并使用骑士逃脱的路线。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">方法七：</span>
<br><span class="rvts21">杀出欢乐堡要塞的海岸边（那里有一群待处决的沉默源力者），然后沿着海岸线逃脱欢乐堡。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



